<template>
  <div class="payment-05">
    <div class="area-1 m-10-auto">
      {{ $t('42') }}
    </div>
    <div class="area-2 m-10-auto">
      <span class="fs-15 bold m-10-auto">{{ info.name }}</span>
      <div class="price m-10-auto">{{ '¥' + formatNumber(info.price) + '/ 月' }}</div>
      <div class="cart-btn" @click="addToCart"><Cart />{{ $t('44') }}</div>
    </div>
  </div>
</template>

<script>
import i18n from '@@/lang/components-desktop/payment-payment02.json'
import Cart from '@@/../components/icons/Cart.vue'
export default {
  i18n: {
    messages: i18n,
  },
  components: {
    Cart,
  },
  computed: {
    info() {
      return this.$store.state.cart.info
    },
  },
  props: {
    getProductPassword: {
      type: Function,
    },
  },
  methods: {
    addToCart() {
      if (this.cartProcesssing) {
        return
      }
      this.cartProcesssing = true
      this.$store.dispatch('cart/addToCart', {
        productId: this.info.productId,
      })
    },
  },
}
</script>

<style lang="scss" scoped>
.area-1 {
  font-size: 20px;
  padding: 10px 20px;
  background: url("data:image/jpeg;base64,");
  margin: 0 auto;
}
.area-2 {
  font-size: 20px;
  padding: 10px 20px 20px 20px;
  background: url("data:image/jpeg;base64,");
}
.fs-15 {
  font-size: 15px;
}
.m-10-auto {
  margin: 10px auto;
}
.bold {
  font-weight: bold;
}
.price {
  background: #666;
  width: 100%;
  color: white;
  border-radius: 5px;
}
.cart-btn {
  background: #ff8400;
  width: 100%;
  color: white;
  border-radius: 5px;
  height: 55px;
  padding: 5px;
  font-size: 17px;
  line-height: 45px;
  cursor: pointer;
  &:active {
    /* box-shadow: inset 0 2px #b85; */
    box-shadow: inset 0 3px 5px #b85;
    transform: translateY(4px);
  }
  &:hover {
    opacity: 0.8;
  }
  .icon-cls {
    width: 41px;
    height: 29px;
    /deep/ svg {
      height: 42px;
    }
  }
}
</style>



